<template>
  <div id="analysis-overview">
    <div v-if="this.$store.state.username == null">
      login first
    </div>
    <div v-else>
      <a-row :gutter='16' v-for="(item, rowNO) in new Array(rowCounter)" :key="rowNO">
        <a-col :span='8' v-for="(device, index) in devices.slice(rowNO * 3, rowNO * 3 + 3)" :key="index">
          <a-card class="box">
            {{records[rowNO * 3 + index]}}
            <!-- {{devices[0].time}} -->
            <a-button @click="refresh">刷新</a-button>
          </a-card>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
import {Row, Col, Card, Button} from 'ant-design-vue'

export default {
  components: {
    ARow: Row,
    ACol: Col,
    ACard: Card,
    AButton: Button
  },
  methods: {
    refresh() {
      const ip = this.$store.state.serverIP
      const getRouter = ip + '/cam/refresh'
      this.axios.get(getRouter)
        .then(response => {
          console.log(response)
          const data = response.data
          if(data.code === 200) {
            this.$store.commit('refresh', data.data)
          }
        })
    },
  },
  computed: {
    devices: function() {
      const devices = this.$store.state.devices
      return devices
    },
    records: function() {
      const records = this.$store.state.records
      return records
    },
    rowCounter() {
      return Math.ceil(this.$store.state.devices.length / 3)
    },
    
  }
}
</script>

